/* noto-sans-jp-regular - latin */
@font-face {
  font-family: 'Noto Sans JP';
  font-style: normal;
  font-weight: 400;
  src: url('./noto-sans-jp-v27-latin-regular.eot'); /* IE9 Compat Modes */
  src: local('Noto Sans Japanese Regular'), local('NotoSansJapanese-Regular'),
    url('./noto-sans-jp-v27-latin-regular.eot?#iefix')
      format('embedded-opentype'),
    /* IE6-IE8 */ url('./noto-sans-jp-v27-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */ url('./noto-sans-jp-v27-latin-regular.woff')
      format('woff'),
    /* Modern Browsers */ url('./noto-sans-jp-v27-latin-regular.svg#NotoSansJP')
      format('svg'); /* Legacy iOS */
  font-display: swap;
}
